import {BasicConfig} from '../Container/types';

export const validEventTrigger = {
    'click': 'onClick',
    'change': 'onChange',
    'treeCheck': 'onCheck',
    'treeSelect': 'onSelect',
    'submitSuccess': ''
};

export class TriggerItem {
    /**
     * 
     */
    eventType: string;

    /**
     * 
     */
    target?: string;

    /**
     * 
     * data -->  @default
     * link --> 
     */
    triggerType?: 'data' | 'link';

    /**
     * 
     */
    href?: string;

    /**
     * encodeURIComponent
     */
    isRaw: boolean;
    
    /**
     * 
     */
    ship?: Object;
}

export class TriggerConfig extends BasicConfig {
    trigger?: TriggerItem[];
}



